<template lang="pug">
.product-list
  .table-header.clfix
    el-input.fl(placeholder='', icon='search', v-model.lazy='filter.name')
    el-select.fr(placeholder="", v-model="filter.platform")
      el-option(v-for="o in platforms", :label="o.name", :value="o.value")
  el-table(:data="filterProducts", height="250", ref="productsTable", @selection-change="checkProduct")
    el-table-column(type="selection", width="45")
    el-table-column(property="name", label='')
    el-table-column(property='platform', label='')
</template>

<script>
import {
  each,
  map,
  merge,
  includes,
  filter
} from 'lodash'

export default {
  props: {
    onChange: {
      type: Function,
      default: function() {

      }
    },
    checkedList: {
      type: Array,
      default () {
        return []
      }
    }
  },

  watch: {
    checkedList: {
      immediate: true,
      handler() {
        this.checkedListClone = merge([], this.checkedList)
        this._updateCheckedStatus()
        this._tableCheckedUpdate()
      }
    }
  },

  methods: {
    _updateCheckedStatus() {
      const names = map(this.checkedListClone, 'name')
      each(this.products, v => {
        v.checked = includes(names, v.name)
      })
    },

    _tableCheckedUpdate() {
      this.$nextTick(() => {
        each(this.products, (v) => {
          this.$refs.productsTable.toggleRowSelection(v, v.checked)
        })
      })
    },

    checkProduct(val) {
      if (this.filterStatus) {
        this.filterStatus = false
        return
      }

      this.checkedListClone = val
      this.onChange(this.checkedListClone)
    }
  },

  computed: {
    filterProducts() {
      this.filterStatus = true
      this._tableCheckedUpdate()
      return filter(this.products, v => {
        return ~v.name.indexOf(this.filter.name) && ~v.platform.indexOf(this.filter.platform)
      })
    }
  },

  data() {
    return {
      filterStatus: false, //checkProduct emit select-change
      checkedListClone: [],
      filter: {
        name: '',
        platform: ''
      },
      platforms: [{
        name: '',
        value: ''
      }, {
        name: '',
        value: ''
      }, {
        name: '',
        value: ''
      }, {
        name: '360',
        value: '360'
      }, {
        name: '',
        value: ''
      }, {
        name: '',
        value: ''
      }],
      products: [{
        name: 'A',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: 'B',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: '1',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: '1',
        amount: ' 78,613,007.00',
        checked: false,
        platform: '360'
      }, {
        name: '',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: 'D2',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: '3601',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }, {
        name: '5001',
        amount: ' 78,613,007.00',
        checked: false,
        platform: ''
      }]
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/_vars.scss';
.el-form-item.is-error .table-header {
  .el-input__inner,
  .el-textarea__inner {
    border-color: $border-color;
  }
}

.table-header {
  padding: 8px 5px;
  background: #f3f6f8;
  border: 1px solid $border-color;
  border-bottom: 0;
  .el-input,
  .el-select {
    max-width: 150px;
  }
}
</style>



