<template lang="pug">
  section.account-list
    .box
      .box-tab-header
        el-button(type="primary", size="small", @click="openAccountDialog()")
          i.icon-batonx.icon-plus
          | 
      el-tabs(v-model='tab', type='card', @tab-click='tabClick')
        el-tab-pane(label='', name='first')
          .filters
            el-input(placeholder='', icon='search', v-model.lazy='filter.belongto')
            el-input(placeholder='', icon='search', v-model.lazy='filter.name')
            el-input(placeholder='', icon='search', v-model.lazy='filter.bankNum')
            el-select(v-model="filter.type", placeholder="")
              el-option(v-for="t in accountTypes", :value="t.value", :label="t.name")
            el-button(size="small", type="primary", @click="clearFilter")  
        el-tab-pane(label='', name='second')
          .filters
            el-input(placeholder='', icon='search', v-model.lazy='filter.belongto')
            el-input(placeholder='', icon='search', v-model.lazy='filter.name')
            el-input(placeholder='', icon='search', v-model.lazy='filter.bankNum')
            el-select(v-model="filter.type", placeholder="")
              el-option(v-for="t in accountTypes", :value="t.value", :label="t.name")
            el-button(size="small", type="primary", @click="clearFilter")  
    .table-container
      el-table(:data='filterAccounts', style='width: 100%')
        el-table-column(prop='belongto', label='', width='220')
          template(scope='scope')
            el-popover(v-if="scope.row.note", trigger='hover' placement='top')
              p : {{ scope.row.note }}
              .name-wrapper(slot='reference')
                | {{ scope.row.belongto }}
                i.icon-batonx.icon-explain
            span(v-if="!scope.row.note") {{scope.row.belongto}}
        el-table-column(prop='type', label='', width='120')
        el-table-column(prop='name', label='', width='220')
        el-table-column(prop='bankNum', label='', width='220')
        el-table-column(prop='bank', label='', width='220')
        el-table-column(label='', width='100')
          template(scope="scope")
            a(@click="openRelationProducts(scope.row)")
              i.icon-batonx.icon-relation
        el-table-column(prop='updateDate', label='', width='120')
        el-table-column(label='', :fixed="fixed", width='100')
          template(scope="scope")
            .operations
              i.icon-batonx.icon-edit(@click="openAccountDialog(scope.row)")
              i.icon-batonx.icon-delete(@click.stop="deleteAccount(scope.row)")
      el-pagination(@size-change='pageSizeChange', @current-change='pageChange', :current-page='page.current', :page-sizes="page.sizes", :page-size="page.size", layout='total, prev, pager, next, jumper', :total='accounts.length')
    el-dialog(title='', v-model='relationProductsVisible')
      el-table(:data='relationProducts')
        el-table-column(property='name', label='', width='200')
        el-table-column(prop="amount", label='')
        el-table-column(property='platform', label='')
    account-dialog(ref="accountDialog", @account-save="onAccountSave")
</template>

<script>
import {
  remove,
  filter,
  mergeWith,
  each,
  find
  // findIndex
} from 'lodash'
import AccountDialog from '@/components/AccountDialog.vue'
import {
  MessageBox
} from 'element-ui'
import moment from 'moment'
import {
  mergeArrayCover
} from '@/common/merge-rules.js'

export default {
  components: {
    AccountDialog
  },
  methods: {
    tabClick(tab, event) {
      console.log(tab, event)
    },

    clearFilter() {
      each(this.filter, (v, k) => {
        this.filter[k] = ''
      })
    },

    pageChange(val) {
      console.log(`${val}`)
    },

    pageSizeChange(val) {
      this.page.size = val
    },

    openAccountDialog(account) {
      this.$refs.accountDialog.open(account)
    },

    openRelationProducts(account) {
      this.relationProducts = account.relationProducts
      this.relationProductsVisible = true
    },

    onAccountSave(account) {
      const activeAccount = find(this.accounts, v => v.id === account.id)
      if (activeAccount) {
        account.updateDate = moment().format('YYYY-MM-DD')
        mergeWith(activeAccount, account, mergeArrayCover)
      } else {
        this.accounts.unshift(account)
      }
    },

    deleteAccount(account) {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.accounts = remove(this.accounts, (v) => {
          return account.id !== v.id
        })
      })
    }
  },

  computed: {
    filterAccounts() {
      return filter(this.accounts, v => {
        return ~v.belongto.indexOf(this.filter.belongto) && ~v.name.indexOf(this.filter.name) && ~v.bankNum.indexOf(this.filter.bankNum) && ~v.type.indexOf(this.filter.type)
      })
    }
  },

