<template lang="pug">
  .account-dialog
    el-dialog(:title="title", v-model="dialogVisible",  @open="onDialogOpen")
      el-form(:model='account', :rules="rules", ref="accountForm", :label-width='formLabelWidth')
        el-form-item(prop="belongto", label='')
          el-input(v-model='account.belongto', auto-complete='off', placeholder='')
        el-form-item(prop="name", label='')
          el-input(v-model='account.name', auto-complete='off', placeholder='')
        el-form-item(prop="bankNum", label='')
          el-input(v-model='account.bankNum', auto-complete='off', placeholder='')
        el-form-item(prop="bank", label='')
          el-input(v-model='account.bank', auto-complete='off', placeholder='')
        el-form-item(prop="type", label='')
          el-select(v-model='account.type', placeholder='')
            el-option(label='', value='')
            el-option(label='', value='')
            el-option(label='', value='')
            el-option(label='', value='')
            el-option(label='', value='')
        el-form-item.is-required(prop="relationProducts", label='')
          input(type="hidden", v-model="account.relationProducts")
          .relation-products-tag(v-show="account.relationProducts && account.relationProducts.length")
            | 
            span(v-for="r in account.relationProducts")
              | {{r.name}}
              i.icon-batonx.icon-close(@click="removeRelationProduct(r)")
          product-list(:on-change="onProductCheckChange", :checked-list="account.relationProducts")
      .dialog-footer(slot="footer")
        el-button(type="primary", size="small", @click='accountSave') 
        el-button(type='gray', size="small", @click='dialogVisible = false') 
</template>

<script>
import ProductList from '@/components/ProductList.vue'
import {
  mergeWith,
  merge,
  uniqueId,
  remove
} from 'lodash'
import {
  validateArray
} from '@/common/validator.js'
import {
  mergeArrayCover
} from '@/common/merge-rules.js'

export default {
  components: {
    ProductList
  },
  methods: {
    open(account = {
      id: null,
      name: '',
      belongto: '',
      bankNum: '',
      bank: '',
      type: '',
      relationProducts: []
    }) {
      this.dialogVisible = true
      this.$nextTick(() => {
        this.account = mergeWith({}, this.account, account, mergeArrayCover)
        this.title = account.id ? '' : ''
      })
    },

    onDialogOpen() {
      this.$nextTick(() => this.$refs.accountForm.resetFields())
    },

    removeRelationProduct(product) {
      this.account.relationProducts = remove(this.account.relationProducts, v => v.name !== product.name)
    },

    onProductCheckChange(products) {
      this.account.relationProducts = merge([], products)
    },

    accountSave() {
      this.$refs.accountForm.validate(valid => {
        if (valid) {
          this.dialogVisible = false
          if (!this.account.id) this.account.id = uniqueId('account_')
          this.$emit('account-save', this.account)
        }
      })
    }
  },

  data() {
    return {
      title: '',
      account: {
        id: null,
        name: '',
        belongto: '',
        bankNum: '',
        bank: '',
        type: '',
        relationProducts: []
      },
      rules: {
        belongto: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        bankNum: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        bank: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        type: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        relationProducts: [{
          validator: validateArray,
          fieldName: '',
          trigger: 'change'
        }]
      },
      formLabelWidth: '120px',
      dialogVisible: false
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/_vars.scss';
.relation-products-tag {
  font-size: 12px;
  span {
    padding: 5px 8px;
    border-radius: 3px;
    background: $border-color;
    margin-right: 10px;
    display: inline-block;
    line-height: 1;
    .icon-close {
      cursor: pointer;
      color: #929aa3;
      margin-left: 10px;
      display: inline-block;
      transform: scale(.7);
      &:hover {
        color: $color-blue;
      }
    }
  }
}
</style>



